@import url(common.scss);


$subtitle-font:'S-CoreDream-3Light';
$content-font: 'SUITE-Regular';
$content-main-font:'SBAggroB';
// font-family: 'SUITE-Regular';

@media screen and (min-width: 1365px) {
  body{
    font-size: 19px;
    //background: red;

    // content5 cjone
    .content5{
      width: 100%;height: 1100px;
      // min-width: 1550px;
      position: relative;
      //background: #ffffff;
      display: flex;
      //flex-direction: row-reverse;
      opacity: 0;
      transition: all 1s linear 0s;
      &.on{
        opacity: 1;
    
        .box_content5{
          //background: red;
          width: 45%;
          .box_in{
            width: 1%;
            height: 1%;
            animation: boxplus 2s cubic-bezier(0.16, 1, 0.3, 1) 0.5s forwards;
            .box_ani{
              opacity: 0;
              animation: box_opa 0.5s linear 2s forwards;
            }
            @keyframes box_opa {
              0%{opacity: 0;}
              100%{opacity: 1;}
            }
           /*  h2{opacity: 0;}
            .content2_rotate{opacity: 0;}
            .content2_box_in{opacity: 0;}
            .kakao_mokup{opacity: 0;} */
          }
          @keyframes boxplus {
            0%{width: 1%;
              height: 1%;}
            25%{}
            50%{width: 1%;height: 750px;}
            75%{}
            100%{
              width: 66%;
              height: 750px;}
          }
        }
    
        .letter_name{
          opacity: 0;
          transform: translateX(100px);
          animation: nameletter 1s linear 0s forwards;
        }
        @keyframes nameletter {
          0%{opacity: 0;transform: translate(100px);}
          100%{opacity: 1;transform: translate(0);}
        }
      }
    
      .box_content5{
        //background: red;
        width: 45%;
        .box_in{
          position: relative;top: 50%;
          transform: translateY(-50%);
          //font-family: $h1-font;
          margin: 0 auto;
          width: 70%;
          height: 750px;
          border: 3px solid #f22259c9;
          border-radius: 10px;
          color: #333333;
          .box_ani{
          h2{
            color: #F22259;
            //font-family: $content-main-font;
            text-indent: 5px;
            font-size: 2rem;
            padding: 0.8rem;
            font-weight: 500;
            text-shadow: 1px 1px 2px #b9b9b9;
          }
    
          .content5_rotate{
            &:nth-of-type(1){
             display: inline-block;
             transform: rotate(270deg);
             position: absolute;left: -75px;top: 340px;
             font-size:1rem;
            }
            &:nth-of-type(2){
             display: inline-block;
             transform: rotate(90deg);
             position: absolute;right: -70px;top: 120px;
             font-size:1rem;
            }
            &:nth-of-type(3){
             position: absolute;
             transform: rotate(270deg);
             bottom: 190px;left: -20px;
             font-size: 2.1rem;
            }
             }
    
             .content5_box_in{
              // 두개
              width: 100%;
              position: absolute;bottom: 0;left: 0;
              display: flex;
              ul{
                font-size: 0.8rem;
                margin: 0 auto;
                &:nth-of-type(1){
                padding: 5px 15px;
                 width:50%;
                 li{
                  display: flex;
                  justify-content: space-between;
                  //color: #fff;
                 }
                }
                &:nth-of-type(2){
                  padding: 0px 15px;
                  width: 50%;
                }
                li{
                 // color: #fff;
                }
              }
            }
            .cj_main_mokup{
              width: 100%;height: 100%;
              position: absolute;top: 10%;left: 10%;
              
            .cj_mokup_main_mo{
              //전체적인 크기
               position: absolute;
               top: 0px;left:50%;
               transform: translateX(-50%);    
               width: 330px;height: 550px;
               transition: all 0.2s linear 0s;
               animation: mainka 2s linear infinite;
        
             .cj_fram_main_mo{
               //틀
               margin-left: 4px;margin-top:22px;
               width: 75%;height: 90%;
               border-radius: 15px 5px 32px 32px;
              background: #101010;
               overflow: hidden;
            
               .cj_backmokup_main_mo{
                //틀 안에 들어갈것
                position: relative;top: -49px;left: 5px;
                display: block;
                width: 250px;height: 605px;
                 video{
                  object-fit: contain;width: 100%;height: 100%;
                   }
                 }
             }
      
             .cj_mokup_img_main_mo{
               //목업
               position: absolute;left: 0;top: 0;clear: both;
               display: block;
               width: 260px;height: 530px;
               background:url(../img/netmain.png)no-repeat 0 0;
               background-size: contain;
             }
            }
            }
          }
      }
    }
    
    
      .cj_box_groub{
        //background: blue;
        position: relative;top: 0;left: 0;
        width: 55%;
        height: 100%;
        // 1
        .content5_box1{
          position: absolute;top: 17%;left:0%;
          //background: greenyellow;
          //z-index: 10;
          //position: absolute;right: 5%;top: 25%;
          .letter_name{
            color: #F22259;
            text-align: center;
            font-size: 4rem;
            font-family: $content-main-font;
            //font-weight: bold;
            //text-shadow: 2px 2px 2px #656565;
            span{
              text-shadow: none;
              padding-left: 6px;
              font-size: 1.8rem;
              color: #ff97b3;
              display: block;
              text-align: left;
              line-height: 1rem;
            }
          }
        }
    
        // 2
        .content5_box2{
          position: absolute;top: 29%;left:0%;
          //background: pink;
          .workP{
            width: 63%;
            text-align: right;
            font-size: 1.18rem;
            font-weight: bold;
            color: #1b1b1b;
            font-family: $subtitle-font;
            strong{
              color: #F22259;
              font-weight: bold;
              //text-shadow: 1px 1px 1px #1b1b1b;
              padding: 0 3px;
            }
          }
          .workul1{
            width: 80%;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: flex-start;
            padding: 25px 3px;
            
            li{
              font-weight: bold;
              font-size: 1rem;
              //font-size: 16px;
              // color: #E76623;
              // font-weight: bold;
              // line-height: 27px;
              strong{
                padding-left: 15px;
              }
               &:nth-of-type(2){
                padding-left: 50px;
               }
               &:nth-of-type(3){
                display: flex;
                align-items: center;
                
                strong{
                  font-size: 1.1rem;
                  display: flex;
                  letter-spacing: 1px;
                  padding: 5px 20px;
                  span{
                    font-size: 1.3rem;
                    padding:0 6px;
                  }
                }
               }
             
            }
            
          }
    
        }
    
        .borderline{
          position: absolute;top: 45%;right: 0;
          display: block;
          width: 60%;height: 3px;
          background: #f22259c6;
        }
    
        // 3
        .content5_box3{
          position: absolute;right: 10%;top: 20%;
          //width: 100%;height: 100%;
          //background: blue;
          // margin-top: 50px;
          display: flex;
          flex-direction: column;
          align-items: flex-end;
          button{
            cursor: pointer;
            // background: #ff90ad;
            color: #181818;
            background: #e5e5e5;
            border: none;
            margin:18px 0;
            padding: 17px 32px;
            font-size: 1rem;
            border-radius: 50px 50px 2px 50px;
            font-weight: bold;
            transition: all 0.2s linear 0s;
            &:hover{
              background: #F22259;
              color: #ffffff;
              box-shadow: 4px 4px 2px #8b0025;
            }
          }
        }
    
        // 4
          .content5_box4{
          //background: yellow;
          position: absolute;top: 50%;left: 0%;
          width: 85%;height: 30%;
          div{
            width: 100%;height: calc(100% / 3);
            display: flex;
            flex-direction: row;
            align-items: center;
          // border: 1px solid black;
            span{
              display: block;
              text-align: center;
              width: 70px;min-width: 70px;
              height: 70px;min-height: 70px;
              border-radius: 50%;
              background: #ffdbe5;
              margin-right: 30px;
              svg{
                display: inline-block;
                font-size: 2.3rem;
                transform: translate(1px, 14px);
              }
            }
              ul{
                li{
                  font-size: 1rem;
                  line-height: 1.6rem;
                  strong{
                   color: #F22259;
                   font-size: 1.1rem;
                  }
                }
              }
            
          }
        }
    
              
      }    
      }
  }
}
