   .faceani{
     text-align: center;
     position:absolute;top: 70%;left: 50%;
     transform: translateX(-50%);
     width: 100%;height: 40%;
     .emoji{position: relative;
       display: inline-block;width: 120px;height: 120px;border-radius: 50%;margin: 0px;background: #ffda6a;
       transform: scale(0.6);
     
       &::after{content: "Emoji";position: absolute;bottom: -40px; left: calc(50% - 30px);width: 60px;font-size: 18px;color: #8a8a8a;}
     }
   
     /* 1. emoji_good */
   .emoji_good{
     background: #548dff;
     &::after{content: "Like";}
     
     .emoji_hand{position: absolute;left: 25px;bottom: 30px;
       width: 20px;height: 40px;background: #fff;
       border-radius: 5px;
     animation: hands-up 2s linear 0s infinite;
   
     &::before{content: "";display: block;
       position: absolute;left: 25px;bottom: 5px;width: 40px;height: 10px;background: #fff;border-radius: 2px 10px 10px 2px;
       box-shadow: 1px -9px 0 1px white,2px -19px 0 2px white,3px -29px 0 3px white;}
     
       .emoji_thumb{position: absolute;right: -25px;top: -25px;
         border-bottom: 20px solid white;
         border-left: 20px solid transparent;
         transform: rotate(5deg);transform-origin: 0 100%;
         animation:thumbs-up 2s linear 0s infinite ;
       
       &::before{
         content: "";position: absolute;left: -9.7px;top: -9px;width: 10px;height: 12px;background: #fff;
         border-radius: 50% 50% 0 0;box-shadow: -1px 4px 0 -1px #fff;transform: rotate(-14deg);transform-origin: 100% 100%;}
       }
   
         @keyframes thumbs-up {
           25%{transform: rotate(20deg);}
           50%, 100%{transform: rotate(5deg);}
           
         }
     
     } 
       @keyframes hands-up {
       25%{transform: rotate(15deg);}
       50%{transform: rotate(-15deg) translateY(-10px);}
       75%,100%{transform: rotate(0deg);}
     }
   
   }
   
   /* 2.imoji_love */
   .emoji_love{
     background: #f55064;
     &::after{content: "Love";}
   
     .emoji_heart{position: absolute;left: 50%;top:50%;
       margin-top: -40px; margin-left: -40px;
       width: 80px;height: 80px;
     animation: heart-beat 1s linear infinite alternate;
      &::before{
       content: "";position: absolute;left: 50%;top: 50%;width: 40px;height: 64px;
       margin-left: -20px;margin-top: -32px;background: #fff;border-radius: 20px 20px 0 0 ;
       transform-origin: 0 100%;transform:translateX(20px) rotate(-45deg);
          }
       &::after{content: "";position: absolute;left: 50%;top: 50%;width: 40px;height: 64px;
         margin-left: -20px;margin-top: -32px;background: #fff;border-radius: 20px 20px 0 0 ;
         transform-origin: 100% 100%;transform:translateX(-20px) rotate(45deg);}
       
   
   }
     @keyframes heart-beat {
       25%{transform:scale(1.1)}
       75%{transform:scale(0.6)}
     }
   }
   
   // haha
   .emoji_haha{
   
     &::after{content: "HAHA";}
   
     .emoji_face{
       position: absolute;width: 100%;height: inherit;
       animation: haha-face 2s linear infinite normal;
   
       .emoji_eyes{
         position: absolute;left: 50%;top: 35px;margin-left: -13px;width: 26px;height: 6px;border-radius: 2px;/*background: #fff;*/
         box-shadow: -25px 5px 0 0 #000,25px -5px 0 0 #000;transform: rotate(20deg);
   
         &::after{
           content: "";
           position: absolute;left: 0;top: 0;
           width: 26px;height: 6px;border-radius: 2px;/*background: red;*/
           box-shadow: -25px -5px 0 0 #000,25px 5px 0 0 #000;
           transform: rotate(-40deg);
         }
       }
   
       .emoji_mouth{
         position: absolute;left: 50%;top: 50%;
         width: 80px;height: 40px;
         margin-left: -40px ;
         border-radius: 0 0 40px 40px;background: #000;
         overflow: hidden;
         animation: haha-moth 2s linear infinite normal;
   
         .emoji_tongue{
           position: absolute;left: 50%;bottom: -10px;margin-left: -35px;
           width: 70px;height: 30px;border-radius: 50%;background: #f55064;
         }
     }
   
     @keyframes haha-moth {
       10%{transform: scale(0.6);top: 45%;}
       20%{transform: scale(0.8);top: 45%;}
     
       30%{transform: scale(0.6);top: 45%;}
       40%{transform: scale(0.8);top: 45%;}
     
       50%{transform: scale(0.6);top: 45%;}
     
       60%{transform: scale(1);top: 50%;}
       70%{transform: scale(1.2);top: 50%;}
     
       80%{transform: scale(1);top: 50%;}
       90%{transform: scale(1.2);top: 50%;}
     }
     }
   
     @keyframes haha-face {
       10%{transform:translateY(25px) ;}
       20%{transform:translateY(15px) ;}
     
       30%{transform:translateY(25px) ;}
       40%{transform:translateY(15px) ;}
     
       50%{transform:translateY(25px) ;}
     
       60%{transform:translateY(0) ;}
       70%{transform:translateY(-10px) ;}
     
       80%{transform:translateY(0) ;}
       90%{transform:translateY(-10px) ;}
     }
   }
   
   /* 4.emioji_yay */
   .emoji_yay{
     &::after{
       content: "YAy";
     }
   
     .emoji_face{
       position: absolute;width: 100%;height: inherit;
       animation:  yay 1s linear infinite alternate;
     
       .emoji_eyebrows{position: absolute;left: calc(50% - 3px);top: 30px ;width: 6px;height: 6px;border-radius: 50%;/*background: #fff;*/box-shadow: -6px 0 0 0 #000,-36px 0 0 0 #000,6px 0 0 0 #000,36px 0 0 0px #000;
       
       
       &::before{
         content: "";position: absolute;left: calc(50% - 18px);bottom: 3px;width: 36px;height: 18px;;border: 6px solid black;border-radius: 60px 60px 0 0;
         border-bottom: 0;box-sizing: border-box;margin-left: -21px;
       }
   
       &::after{
         content: "";position: absolute;left: calc(50% - 18px);bottom: 3px;width: 36px;height: 18px;;border: 6px solid black;border-radius: 60px 60px 0 0;border-bottom: 0;box-sizing: border-box;margin-left: 21px;
       }
     }
   
     .emoji_mouth{
       position: absolute;left: calc(50% - 3px);
       top: 60px;width: 6px;height: 6px;border-radius: 50%;box-shadow: -25px 0 0 0 #000,25px 0 0 0 #000
       /*보조개*/
       ,-35px -2px 30px 10px #D5234C,35px 2px 30px 10px #D5234C;
   
       &::after{
         content: "";
         position: absolute;left: calc(50% - 40px);top: -64px;width: 80px;height: 80px;border-radius: 50%;box-sizing: border-box;
         border: 6px solid #000;
         border-top-color: transparent ;
         border-right-color: transparent ;
         border-left-color: transparent ;
       }
     }
     }
       
       @keyframes yay {
         25%{transform: rotate(-15deg);}
         75%{transform: rotate(15deg);}
       }
   }
   
   /* 5.emoji_wow */
   .emoji_wow{
     &::after{content: "WOW";}
   
     .emoji_face{position: relative;width: 100%;height: inherit;
       animation: wow-face 3s linear 0s infinite;
     
       .emoji_eyebrows{
         position: absolute;left: calc(50% - 3px);
         width: 6px;height: 6px;border-radius: 50%;
         box-shadow: -18px 0 0 0 #000,-33px 0 0 0 #000,18px 0 0 0 #000,33px 0 0 0 #000;
         animation:wow-brow 3s linear 0s infinite ;
       
         &::before{
           content: "";
           position: inherit;align-self: baseline;left: calc(50% - 12px);top: -3px;width: 24px;height: 20px;border-radius: 50%;border: 6px solid #000;box-sizing: border-box;
           border-right-color: transparent;
           border-bottom-color: transparent;
           border-left-color: transparent;
           margin-left: -25px;
         }
         &:after{
           content: "";
           position: inherit;align-self: baseline;left: calc(50% - 12px);top: -3px;width: 24px;height: 20px;border-radius: 50%;border: 6px solid #000;box-sizing: border-box;
           border-right-color: transparent;
           border-bottom-color: transparent;
           border-left-color: transparent;
           margin-left: 25px;
         }
       }
         @keyframes wow-brow {
           15%,65%{top: 25px;}
           75%,100%,0%{top: 15px;}
         }
   
         .emoji_eyes{
           position: absolute;left: calc(50% - 8px);
           top: 35px;width: 16px;height: 24px;border-radius: 50%;box-shadow: 25px 0 0 0 #000,-25px 0 0 0 #000;
         }
   
         .emoji_tongue{
           position: absolute;left: calc(50% - 15px);top: 50%;width: 30px;height: 45px;border-radius:50%;background: #000;
           animation: wow-mouth 3s linear 0s infinite;
         }
           @keyframes wow-mouth {
             10%{width: 20px;height: 20px;left: calc(50% - 10px);}
             30%{width: 20px;height: 20px;left: calc(50% - 10px);}
             50%{width: 30px;height: 40px;left: calc(50% - 15px);}
             70%{width: 30px;height: 40px;left: calc(50% - 15px);}
             75%{height: 50px;}
             100%{height: 50px;}
           }
       }
       @keyframes wow-face {
         15%{transform: rotate(20deg) translateX(-25px);}
         25%{transform: rotate(20deg) translateX(-25px);}
         45%{transform: rotate(-20deg) translateX(25px);}
         55%{transform: rotate(-20deg) translateX(25px);}
         75%{transform: rotate(0) translateX(0);}
         100%{transform: rotate(0) translateX(0);}
       }
   }
   /* 6.emoji_sad */
   
   .emoji_sad{
     &::after{content: "Sad";}
   
     .emoji_face{
       position: relative;width: 100%;height: inherit;
       animation: sad-face 2s ease-in 0s infinite;
   
       .emoji_eyebrows{
         position: absolute;left: calc(50% - 3px); top: 35px;width: 6px;height: 6px;border-radius: 50%;box-shadow: -40px 9px 0 0 #000,-25px 0 0 0 #000, 25px 0 0 0 #000, 40px 9px 0 0 #000;
         &::before{
           content: "";
           position: absolute;left: calc(50% - 15px);top: 2px;border-radius: 50%;width: 30px;height: 20px;border: 6px solid #000;
           box-sizing: border-box;
           border-left-color: transparent;
           border-right-color: transparent;
           border-bottom-color: transparent;
           margin-left: -30px;transform: rotate(-30deg);
         }
         &::after{
           content: "";
           position: absolute;left: calc(50% - 15px);top: 2px;border-radius: 50%;width: 30px;height: 20px;border: 6px solid #000;
           box-sizing: border-box;
           border-left-color: transparent;
           border-right-color: transparent;
           border-bottom-color: transparent;
           margin-left: 30px;transform: rotate(30deg);
         }
       }
   
       .emoji_eyes{
         position: absolute;left: calc(50% - 7px);
         top: 50px;width: 14px;height: 16px;border-radius: 50%;
         box-shadow: 25px 0 0 0 #000,-25px 0 0 0 #000;
         &::after{content: "";
           position: absolute;width: 12px;height:12px;margin-left:6px;background: #548dff;border-radius:0 100% 40% 50%/0 50% 40% 100%;
           animation: tear-drop 2s ease-in infinite;}
   
           @keyframes tear-drop {
             0%,100%{display: block;left: 35px;top: 15px;transform: rotate(45deg) scale(0);}
             25%{display: block;left: 35px;transform: rotate(45deg) scale(2);}
             49.9%{display: block;left: 35px;top: 65px;transform: rotate(45deg) scale(0);}
           
             50%{display: block;left: -35px;top: 15px;transform: rotate(45deg) scale(0);}
             75%{display: block;left: -35px;top: 15px;transform: rotate(45deg) scale(2);}
             99.9%{display: block;left: -35px;top: 65px;transform: rotate(45deg) scale(0);}
           
           }
       }
   
       .emoji_mouth{
         position: absolute;
         left: calc(50% - 3px);top: 90px;width:6px;height: 6px;border-radius: 50%;
         box-shadow: -18px 0 0 0 #000,18px 0 0 0 #000;
       &::after{
         content: "";position: absolute;left: calc(50% - 30px);top: -10px;width: 60px;height: 80px;
         box-sizing: border-box;border: 6px solid #000;border-radius: 50%;
         border-right-color: transparent;
         border-bottom-color: transparent;
         border-left-color: transparent;
       }
       }
     }
       @keyframes sad-face {
         25%,35%{top: -15px;}
         55%,95%{top: 10px;}
         100%,0%{top: 0;}
       }
   }
   
   /* 7.emoji_angry */
   
   .emoji_angry{
     background: linear-gradient(to bottom, #D5234c -10%,#ffda6a 100%);background-size: 100%;
     &::after{content: "Angry";}
   
     .emoji_face{
       position: relative;width: 100%;height: inherit;
       animation: angry-face 2s ease-in 0s infinite;
   
       .emoji_eyebrows{
         position: absolute;left: calc(50% - 3px);top: 55px;width: 6px;height: 6px;border-radius: 50%;box-shadow: -44px 5px 0 0 #000,-7px 16px 0 0 #000,7px 16px 0 0 #000,44px 5px 0 0 #000;
         &::before{
           content: "";
           position: absolute;left: calc(50% - 25px);top: 0;width: 50px;height: 20px;border: 6px solid #000;border-radius: 50%;box-sizing: border-box;
           border-top-color: transparent;
           border-right-color: transparent;
           border-left-color: transparent;
           margin-left:-25px;transform: rotate(15deg);
         }
         &::after{
           content: "";
           position: absolute;left: calc(50% - 25px);top: 0;width: 50px;height: 20px;border: 6px solid #000;border-radius: 50%;box-sizing: border-box;
           border-top-color: transparent;
           border-right-color: transparent;
           border-left-color: transparent;
           margin-left: 25px;transform: rotate(-15deg);
         }
       }
       .emoji_eyes{
         position: absolute;left: calc(50% - 6px);
         top: 70px;width: 12px;height: 12px;border-radius: 50%;
         box-shadow: 25px 0 0 0 #000,-25px 0 0 0 #000;
       }
   
       .emoji_mouth{position: absolute;left: calc(50% - 18px);bottom: 15px;width: 36px;height: 18px;background:#000;border-radius: 50%;
         animation: angry-mouth 2s ease-in 0s infinite ;}
         @keyframes angry-mouth {
           25%{height: 6px;bottom: 25px;}
           50%{height: 6px;bottom: 25px;}
     }
   
     @keyframes angry-face {
       35%,60%{transform: translateX(0) translateY(10px) scale(0.9);}
     
       40%{transform: translateX(-5px) translateY(10px) scale(0.9);}
       45%{transform: translateX(5px) translateY(10px) scale(0.9);}
         
       50%{transform: translateX(-5px) translateY(10px) scale(0.9);}
       55%{transform: translateX(5px) translateY(10px) scale(0.9);}
       
     }
   }
   }
   }
 
   }
  
}

