              }
              &:nth-of-type(2){
                animation: listleft 1s ease-in 0.1s forwards;
              }
              &:nth-of-type(3){
                animation: listleft 1s ease-in 0.2s forwards;
              }
              &:nth-of-type(4){
                animation: listleft 1s ease-in 0.3s forwards;
              }
              &:nth-of-type(5){
                animation: listleft 1s ease-in 0.4s forwards;
              }
              &:nth-of-type(6){
                animation: listleft 1s ease-in 0.5s forwards;
              }
