@import url(common.scss);

$main-color:#ffe81c;
$main-background: #ffffff;
$main-font:'SBAggroB';
$sub-font: 'Roboto', sans-serif;


@media screen and (min-width: 1365px) {
  .navigation{
    display: block;
    z-index: 30;
    position: fixed;top: 0;left: 0;
    width: 100%;
    height: 100vh;
    background: #fafafa;
    overflow: hidden;
  
    // 햄버거버튼
    .btnMenu{
      position: absolute;right: 0px;top: 30px;
      z-index:2;
      margin-right: 50px;
      width: 30px;height: 30px;
      display: flex;justify-content: center;align-items: center;
      cursor: pointer;
      transition: all 0.2s ease-in-out;
      .btnMenu_burger{
      width: 30px;height: 3px;border-radius: 5px;
      background-color: $main-color;transition: all 0.2s ease-in-out;
  
      &::after{content: "";position: absolute;
        width: 30px;height: 3px;border-radius: 5px;
        background-color: $main-color;transition: all 0.2s ease-in-out;
        transform: translateY(9px);
      }
  
      &::before{content: "";position: absolute;
        width: 30px;height: 3px;border-radius: 5px;
        background-color: $main-color;transition: all 0.2s ease-in-out;
        transform: translateY(-9px);
      }
      }
        .btnMenu_burger{
          transform: translateX(-50px);
          background-color: transparent;box-shadow: none;
          &::before {
            transform: rotate(45deg) translate(35px, -35px);
            background-color: $main-color;}
          
          &::after {transform: rotate(-45deg) translate(35px, 35px);background-color: $main-color;}
        }
  
      }
  
  .line{
    position: absolute;top: 0;left: 0;
    width: 90%;
    height: 100vh;
    .borderline{height: 100%;
     width: calc(100% / 3);
     display: inline-block;
     border-right: 1px solid $main-color;
     box-sizing: border-box;
     position: relative;}
  
    .borderline:nth-of-type(1){transform: translateY(-1000px);
      animation: ani2 0.5s linear 0s 1;
      animation-fill-mode: forwards;}
    .borderline:nth-of-type(2){transform: translateY(1000px);
      animation: ani3 0.5s linear 0s 1;
      animation-fill-mode: forwards;}
    .borderline:nth-of-type(3){transform: translateY(-1000px);
      animation: ani2 0.5s linear 0s 1;
      animation-fill-mode: forwards;}
  
   }
   @keyframes ani2 {
    0%{transform: translateY(-1000px);}
    95%{transform: translateY(-10px);}
    100%{transform: translateY(0);}
   }
   @keyframes ani3 {
    0%{transform: translateY(1000px);}
    95%{transform: translateY(10px);}
    100%{transform: translateY(0);}
   }
   ul{
      position: relative;top: 200px;left: 0px;
      display: flex;
      justify-content: space-around;
      font-family: $main-font;
      width: 90%;
    li{
      width: calc(100% / 3);
      padding-left: 70px;
      font-size: 2.5rem;
      color: $main-color;
      position: relative;
      height: 100%;
      &:nth-of-type(2){
        transform: translateY(-50px);
      }
      
      div{
        margin-left: 2.5rem;
        width: 100%;
        height: 80vh;
        opacity: 0;
        animation: letter 0.5s linear 0s 1;
        animation-fill-mode: forwards;
        span{
          position: absolute;top: 5px;left: 75px;
          color:transparent;
          -webkit-text-stroke: 1px $main-color;
          animation: letter2 0.5s linear 0s 1;
          animation-fill-mode: forwards;
        }
          .width_border{position: absolute;top: 80px;left: 0;
            width: 0%;border: 1px solid $main-color;
            // 105
            animation: borderline 0.5s linear 0s 1;
            animation-fill-mode: forwards;
          }
          @keyframes letter {
            0%{opacity: 0;margin-left: 50px;}
            100%{opacity: 1;margin-left: 0px;}
          }
          @keyframes letter2 {
            0%{left: 58px;}
            100%{left: 75px;}
          }
  
          @keyframes borderline {
            0%{width: 0%;}
            100%{width: 105%;}
          }
  
          ul{
          position: relative;top: 60px;
          display: flex;flex-direction: column;
          li{
            width: 100%;
            border-right: none;
            line-height: 1.8;
            padding-left: 1.5rem;
            font-size: 1.5rem;
            color: #201c1c;
            letter-spacing: 1px;
            &:nth-of-type(3){
              width: 150%;
            }
            
            &:hover{
              cursor: pointer;
              font-size: 1.6rem;
              color:#ff8800;
              text-shadow: none;
              }
            &:nth-of-type(2){
              transform: translateY(0px);
            }
          }
        }
      }
       
    }
   }
  }
}
