@media screen and (min-width: 1365px){
  .cjonemadal{
    z-index:1;
  width:90vw;
  height:80vh;
  border-radius: 30px;
  background: #ff99aa;
  border: 5px solid #ffffff7f;
  position: relative;
  top:-350px;right:-2%;
  
  .tatal{
    width:100%;
    height:100%;
    overflow-y: auto;
    margin: 0 auto;
    .close{
      //float: right;
      cursor: pointer;
      color: #fff;
      position: absolute;right: 40px;top: 20px;
      font-size: 3rem;
    }
    .inbox_cj{
      //background: yellow;
      width: 100%;height: 100%;
      padding: 10px;
      box-sizing: border-box;
      
      p{
        &:nth-of-type(1){
          font-size: 1.4rem;
          padding: 5px;
          font-weight: bold;}
        font-size: 1rem;
        padding: 10px;
      }
  
      .cjbox{
        margin: 0 auto;
        //background: green;
        width: 90%;height: 1430px;
        // width: 80vw;height: 100%;
         overflow-x: hidden;
        .cjimag{
          //background: red;
          margin: 0 auto;
          width: 100%;height: 95%;
          background: url(../img/cjweb_vali.png)no-repeat 0 0;
          background-size: contain;
          background-position: 50%;
        }
      }
    }
  }
  }
}
