@import url(common.scss);
$main-color:#000000;
$subtitle-font:'S-CoreDream-3Light';

$h1-font:'S-CoreDream-3Light';
$content-font: 'SUITE-Regular';
$content-main-font:'SBAggroB';


@media screen and (min-width: 1365px){
  .main{
    position: relative;top: 0;
    //background:linear-gradient(180deg, rgba(254,254,254,1) 43%, rgba(220,219,218,1) 100%);
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    // &.on{
    //  background: #000;
    // }
  //  .port{
  //   position: absolute;top: 10px;left: 20px;
  //   font-family: $sub-font;
  //   // font-size: 50px;
  //   font-size: 3rem;
  //  }
  /*   h1{
      font-size: 150px;
      font-family: $h1-font;
      font-weight: 300;
      backface-visibility: hidden;
      animation: maintexth1 1s cubic-bezier(0.23, 1, 0.32, 1) 0s;
    } */
  
    // cubic-bezier(0.23, 1, 0.32, 1)
    .titlenames{
      font-size: 8.5rem;
      font-weight: bold;
      font-family: $content-main-font;
      width: 100%;overflow: hidden;
      text-align: center;
      //border: 1px solid red;
      span{
        display: inline-block;
        // transform: translateY(150px);
        &:nth-of-type(1){
          transform: translateY(180px);
          animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0s forwards;
        }
        &:nth-of-type(2){
          transform: translateY(180px);
          animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.2s 
          forwards;
        }
        &:nth-of-type(3){
          transform: translateY(180px);
          animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.4s 
          forwards;
        }
        &:nth-of-type(4){
          transform: translateY(180px);
          animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.6s 
          forwards;
        }
        &:nth-of-type(5){
          transform: translateY(180px);
          animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.8s 
          forwards;
        }
        &:nth-of-type(6){
          transform: translateY(180px);
          animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1s 
          forwards;
        }
        &:nth-of-type(7){
          transform: translateY(180px);
          animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1.2s 
          forwards;
        }
        &:nth-of-type(8){
          transform: translateY(180px);
          animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1.4s 
          forwards;
        }
        &:nth-of-type(9){
          transform: translateY(180px);
          animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1.6s 
          forwards;
        }
        
      }
    }
  
  @keyframes title1 {
    0%{
      transform: translateY(180px);
    }
    100%{
      transform: translateY(0px);
    }
  }
  
  // -----
    .content_main{ 
      width: 13ch;
      animation: typing 2s steps(13) 1.8s , blink .5s step-end infinite alternate, noneblock 1.8s linear 0s;
      white-space: nowrap;
      overflow: hidden;
      border-right: 3px solid;
      font-size: 3rem;
      //font-weight: 500;
      font-family: $h1-font;
      display: flex;
      align-items: center;
      //opacity: 0;
      span{
        //margin: 0 10px;
      }
    }
  
    @keyframes noneblock {
      0%{width: 0}
      100%{width: 0}
    }
  
    @keyframes typing {
      from {
        width: 0
      }
  
    }
        
    @keyframes blink {
      50% {
        border-color: transparent
      }
    }
  
  
  .mainP{
    position: relative;bottom: -10px;
    font-family: $h1-font;
    color: $main-color;
    font-size: 1.5rem;
    opacity: 0;
    transform: scale(1.2);
    animation: mainP 0.5s linear 4.5s forwards;
    &.oou{
      color: red;
    }
  }
  @keyframes mainP {
    0%{opacity: 0;
      transform: scale(1.2);}
    100%{opacity: 1;
      transform: scale(1.0);}
  }
  
  .mouse {
    position: absolute;
    width: 22px;
    height: 42px;
    bottom: 50px;
    left: 50%;
    margin-left: -12px;
    border-radius: 15px;
    border: 2px solid #888;
    animation: intro 1s;
  
    .scroll {
      display: block;
      width: 3px;
      height: 3px;
      margin: 6px auto;
      border-radius: 4px;
      background: #888;
      animation: finger 1s infinite;
    }
  
    p{
      display: block;
      font-size: 0.5rem;
      width: 100px;
      position: absolute;
      top: 45px;left: -13px;
    }
  }
  
  @keyframes intro {
    0% {
      opacity: 0;
      transform: translateY(40px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes finger {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
      transform: translateY(20px);
    }
  }
  
  }
}
