            animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.6s 
            forwards;
          }
          &:nth-of-type(5){
            transform: translateY(180px);
            animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.8s 
            forwards;
          }
          &:nth-of-type(6){
            transform: translateY(180px);
            animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1s 
            forwards;
          }
          &:nth-of-type(7){
            transform: translateY(180px);
            animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1.2s 
            forwards;
          }
          &:nth-of-type(8){
            transform: translateY(180px);
            animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1.4s 
            forwards;
          }
          &:nth-of-type(9){
            transform: translateY(180px);
            animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1.6s 
            forwards;
          }
          
        }
      }
    
    @keyframes title1 {
      0%{
        transform: translateY(180px);
      }
      100%{
        transform: translateY(0px);
      }
    }
    
    // -----
      .content_main{ 
        width: 13ch;
        animation: typing 2s steps(13) 1.8s , blink .5s step-end infinite alternate, noneblock 1.8s linear 0s;
        white-space: nowrap;
        overflow: hidden;
        border-right: 3px solid;
        font-size: 2.5rem;
        //font-weight: 500;
        font-family: $h1-font;
        display: flex;
        align-items: center;
        //opacity: 0;
      }
    
      @keyframes noneblock {
        0%{width: 0}
        100%{width: 0}
      }
    
      @keyframes typing {
        from {
          width: 0
        }
    
      }
          
      @keyframes blink {
        50% {
          border-color: transparent
        }
      }
    
    
    .mainP{
      width: 270px;
      text-align: center;
      position: relative;bottom: -10px;
      font-family: $h1-font;
      color: $main-color;
      font-size: 1.3rem;
      opacity: 0;
      transform: scale(1.2);
      animation: mainP 0.5s linear 4.5s forwards;
      &.oou{
        color: red;
      }
    }
    @keyframes mainP {
      0%{opacity: 0;
        transform: scale(1.2);}
      100%{opacity: 1;
        transform: scale(1.0);}
    }
    
    .mouse {
      position: absolute;
      width: 20px;
      height: 40px;
      bottom: 70px;
      left: 50%;
      margin-left: -12px;
      border-radius: 15px;
      border: 1.2px solid #888;
      animation: intro 1s;
    
      .scroll {
        display: block;
        width: 3px;
        height: 3px;
        margin: 6px auto;
        border-radius: 4px;
        background: #888;
        animation: finger 1s infinite;
      }
    
      p{
        display: block;
        font-size: 1rem;
        width: 100px;
        position: absolute;
        top: 45px;left: -15px;
      }
    }
    
    @keyframes intro {
      0% {
        opacity: 0;
        transform: translateY(40px);
      }
      100% {
        opacity: 1;
        transform: translateY(0);
      }
    }
    
    @keyframes finger {
      0% {
        opacity: 1;
      }
      100% {
        opacity: 0;
        transform: translateY(20px);
      }
    }
    
    }
    .border_blur1{
      z-index: -15;
      position: absolute;bottom: 35%;right: 0;
      width: 200px;height: 200px;
      background: #fff6c0;
      filter: blur(100px);
    }
    .border_blur2{
      z-index: -15;
      position: absolute;bottom: 10%;right: 30%;
      width: 200px;height: 200px;
      background:#ffdfc7;
      filter: blur(100px);
    }
  }
}

