@media screen and (min-width: 1365px){
  .kosonemadal{
    z-index:1;
  width:90vw;
  height:80vh;
  border-radius: 30px;
  background: #abdfff;
  border: 5px solid #ffffff7f;
  position: relative;
  top:-350px;right:-47%;
  
  .tatal{
    width:100%;
    height:100%;
    overflow-y: auto;
    margin: 0 auto;
    .close{
      cursor: pointer;
      //float: right;
      color: #fff;
      position: absolute;right: 40px;top: 20px;
      font-size: 50px;
    }
    .inbox_kos{
      //background: yellow;
      width: 100%;height: 100%;
      padding: 10px;
      box-sizing: border-box;
      
      p{
        &:nth-of-type(1){
          font-size: 1.4rem;
          padding: 5px;
          font-weight: bold;}
        font-size: 1rem;
        padding: 10px;
      }

      .kosbox{
        margin: 0 auto;
        //background: green;
        width: 90%;height: 4800px;
        // width: 80vw;height: 100%;
         overflow-x: hidden;
        .kosimag{
          //background: red;
          margin: 0 auto;
          width: 100%;height: 95%;
          background: url(../img/kosdaq_vali.png)no-repeat 0 0;
          background-size: contain;
          background-position: 50%;
        }
      }
    }
  }
  }
}

