@import url(common.scss);
$subtitle-font:'S-CoreDream-3Light';
$content-font: 'SUITE-Regular';
$content-main-font:'SBAggroB';

@media screen and (min-width: 1365px) {
  body{
    font-size: 19px;
    //background: red; //1434
    .content2{
      width: 100%;height: 1100px;
      min-width: 1444px;
      position: relative;
      //background: #ffffff;
      display: flex;
      flex-direction: row-reverse;
      opacity: 0;
      transition: all 1s linear 0s;
        // 애니메이션 시작
  &.on{
    opacity: 1;

    // box애니메이션
    .box_content2{
      //background: red;
      width: 45%;
      .box_in{
        width: 1%;
        height: 1%;
        animation: boxplus 2s cubic-bezier(0.16, 1, 0.3, 1) 0.5s forwards;
        .box_ani{
          opacity: 0;
          animation: box_opa 0.5s linear 2s forwards;
        }
        @keyframes box_opa {
          0%{opacity: 0;}
          100%{opacity: 1;}
        }
       /*  h2{opacity: 0;}
        .content2_rotate{opacity: 0;}
        .content2_box_in{opacity: 0;}
        .kakao_mokup{opacity: 0;} */
      }
      @keyframes boxplus {
        0%{width: 1%;
          height: 1%;}
        25%{}
        50%{width: 1%;height: 750px;}
        75%{}
        100%{
          width: 66%;
          height: 750px;}
      }
    }

    .letter_name{
      opacity: 0;
      transform: translateX(-100px);
      animation: nameletter 1s linear 0s forwards;
    }
    @keyframes nameletter {
      0%{opacity: 0;transform: translate(-100px);}
      100%{opacity: 1;transform: translate(0);}
    }
  }
// 애니메이션 끝
      .box_content2{
        //background: red;
        width: 45%;
        .box_in{
          position: relative;top: 50%;
          transform: translateY(-50%);
          //font-family: $h1-font;
          margin: 0 auto;
          width: 66%;
          height: 750px;
          border: 3px solid #ffe196;
          border-radius: 10px;
          color: #333333;
          .box_ani{
            width: 100%;height: 100%;
          h2{
            //font-family: $subtitle-font;
            color: #ffe196;
            text-indent: 5px;
            font-size: 2rem;
            padding: 0.8rem;
            font-weight: 500;
            text-shadow: 1px 1px 2px #b9b9b9;
          }
    
          .content2_rotate{
            &:nth-of-type(1){
             display: inline-block;
             transform: rotate(270deg);
             position: absolute;left: -75px;top: 340px;
             font-size:1rem;
            }
            &:nth-of-type(2){
             display: inline-block;
             transform: rotate(90deg);
             position: absolute;right: -80px;top: 140px;
             font-size:1rem;
            }
            &:nth-of-type(3){
             position: absolute;
             transform: rotate(270deg);
             bottom: 190px;left: -20px;
             font-size: 2.1rem;
            }
             }
    
             .content2_box_in{
              // 두개
              width: 100%;
              position: absolute;bottom: 0;left: 0;
              display: flex;
              ul{
                font-size: 0.8rem;
                margin: 0 auto;
                &:nth-of-type(1){
                padding: 5px 15px;
                 width:50%;
                 li{
                  display: flex;
                  justify-content: space-between;
                  //color: #fff;
                 }
                }
                &:nth-of-type(2){
                  padding: 0px 15px;
                  width: 50%;
                }
                li{
                 // color: #fff;
                }
              }
            }
    
            .kakao_mokup{
              width: 100%;height: 100%;
              object-fit: contain;
              position:absolute;top:10%;left: 8%;
              //animation: contentkakao 5s linear 0s infinite;
              //transition: all 0.2s linear 0s;
        /*         &:hover{
                transform: rotateY(20deg);
              } */
            .kakao_mokup_main_mo{
              //전체적인 크기
               position: absolute;
               top:0;left:50%;
               transform: translateX(-50%);   
               width: 330px;height: 550px;
               transition: all 0.2s linear 0s;
               //background: blue;
             .kakao_fram_main_mo{
               //틀
               margin-left: 7px;margin-top:22px;
               width:75%;height: 90%;
               border-radius: 5px 5px 32px 32px;
              background: #131313;
               overflow: hidden;
            
               .kakao_backmokup_main_mo{
                //틀 안에 들어갈것
                position: relative;top: -195px;left: 5px;
                display: block;
                width: 240px;height: 900px;
                overflow: hidden;
                 video{
                  width: 100%;height: 100%;
                  object-fit: contain;//height: 893px;width: 247px;
                   }
                 }
             }
             
             .kakao_mokup_img_main_mo{
               //목업
               position: absolute;left: 0;top: 0;clear: both;
               display: block;
               width: 260px;height: 530px;
               background:url(../img/netmain.png)no-repeat 0 0;
               background-size: contain;
             }
            }
            }
          }
        }
      }
    
    
      .kakao_box_groub{
        //background: blue;
        position: relative;top: 0;left: 0;
        width: 55%;
        height: 100%;
        // 1
        .content2_box1{
          position: absolute;top: 17%;left: 15%;
          //background: greenyellow;
          //z-index: 10;
          //position: absolute;right: 5%;top: 25%;
          .letter_name{
            color: #ffbe1a;
            text-align: center;
            font-size: 4rem;
            font-family: $content-main-font;
            //font-weight: bold;
            //text-shadow: 2px 2px 2px #656565;
            span{
              text-shadow: none;
              font-size: 1.8rem;
              color: #e7dac2;
              display: block;
              text-align: left;
              line-height: 1rem;
            }
          }
        }
    
        // 2
        .content2_box2{
          position: absolute;top: 29%;left: 15%;
          //background: pink;
          .workP{
            font-size: 1.18rem;
            font-weight: bold;
            color: #1b1b1b;
            font-family: $subtitle-font;
            strong{
              color: #ffbe1a;
              font-weight: bold;
              //text-shadow: 1px 1px 1px #a9a9a9;
              padding: 0 3px;
            }
          }
          .workul1{
            
            width: 80%;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: flex-start;
            padding: 25px 0;
            
            li{
              font-weight: bold;
              font-size: 1rem;
              //font-size: 16px;
              // color: #E76623;
              // font-weight: bold;
              // line-height: 27px;
              strong{
                padding-left: 15px;
              }
               &:nth-of-type(2){
                padding-left: 50px;
               }
               &:nth-of-type(3){
                display: flex;
                align-items: center;
                
                strong{
                  font-size: 1.1rem;
                  display: flex;
                  letter-spacing: 1px;
                  padding: 5px 23px;
                  span{
                    font-size: 1.3rem;
                    padding:0 6px;
                  }
                }
               }
             
            }
            
          }
    
        }
    
        .borderline{
          position: absolute;top: 45%;
          display: block;
          width: 60%;height: 3px;
          background: #ffbf57af;
        }
    
        // 3
        .content2_box3{
          position: absolute;right: 0%;top: 20%;
          //width: 100%;height: 100%;
          //background: blue;
          // margin-top: 50px;
          display: flex;
          flex-direction: column;
          align-items: flex-end;
          button{
            cursor: pointer;
            color: #181818;
            background: #e5e5e5;
            border: none;
            margin:18px 0;
            padding: 17px 32px;
            font-size: 1rem;
            border-radius: 50px 50px 2px 50px;
            font-weight: bold;
            transition: all 0.2s linear 0s;
            &:hover{
              background: #ffbe1a;
              color: #ffffff;
              box-shadow: 4px 4px 2px #bf8900;
            }
          }
        }
    
        .content2_box4{
          //background: yellow;
          position: absolute;top: 50%;left: 15%;
          width: 85%;height: 30%;
          div{
            width: 100%;height: calc(100% / 3);
            display: flex;
            flex-direction: row;
            align-items: center;
          // border: 1px solid black;
            span{
              display: block;
              text-align: center;
              width: 70px;min-width: 70px;
              height: 70px;min-height: 70px;
              border-radius: 50%;
              background: #fff3d49d;
              margin-right: 30px;
              svg{
                display: inline-block;
                font-size: 2.3rem;
                transform: translate(1px, 14px);
              }
            }
              ul{
                li{
                  font-size: 1rem;
                  line-height: 1.6rem;
                  strong{
                   color: #ffbe1a;
                   font-size: 1.1rem;
                  }
                }
              }
            
          }
        }
        
      }    
      }
  }
}
